.container {
    display: flex;
    background-color: #462446;
    height: 75px;
    align-items: center;
    font-size: 20px;
    border-bottom: 5px solid navajowhite;
    position: fixed;
    top: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  
    .search {
      flex: 1;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 12px;
  
      p {
        font-family: "Playwrite MX", cursive;
        font-optical-sizing: auto;
        font-weight: 600;
        font-style: normal;
        color: white;
        font-size: 30px;
        transition: color 0.3s ease;
  
        &:hover {
          color: navajowhite;
        }
      }
    }
  
    .second {
      display: flex;
      flex: 1;
      justify-content: space-around;
      padding: 0 70px;
  
      .item {
        color: white;
        transition: color 0.3s ease, background-color 0.3s ease;
        padding: 10px;
        border-radius: 5px;
  
        &:hover {
          color: #462446;
          background-color: navajowhite;
        }
      }
    }
  
    @media (max-width: 1024px) {
      font-size: 18px;
  
      .search p {
        font-size: 28px;
      }
  
      .second {
        padding: 0 50px;
      }
    }
  
    @media (max-width: 768px) {
      flex-direction: column;
      height: auto;
      padding: 10px 20px;
  
      .search {
        padding-left: 0;
        flex-direction: column;
        gap: 6px;
  
        p {
          font-size: 24px;
        }
      }
  
      .second {
        padding: 0;
        justify-content: space-between;
        width: 100%;
  
        .item {
          margin: 10px 0;
          font-size: 16px;
        }
      }
    }
  
    @media (max-width: 480px) {
      .search p {
        font-size: 20px;
      }
  
      .second .item {
        font-size: 14px;
      }
    }
  }
  